*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  cursor: pointer;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

select {
  -ms-appearance: none;
  appearance: none;
}

select::-ms-expand {
  display: none;
}

@font-face {
  src: local(Roboto), local(Roboto-Regular), url("roboto-regular.943bba22.woff2") format("woff2"), url("roboto-regular.b6055c54.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  src: local(Roboto), local(Roboto-Medium), url("roboto-medium.359fd0d1.woff2") format("woff2"), url("roboto-medium.69fc7b3c.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}

@font-face {
  src: local(Roboto), local(Roboto-Bold), url("roboto-bold.5b6b6af5.woff2") format("woff2"), url("roboto-bold.8ee15f53.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

:root {
  --accent-color: #f87719;
  --dark-color: #111;
  --dark-grey-color: #282828;
  --middle-grey-color: #595959;
  --light-grey-color: #b7b7b7;
  --light-color: #f8f8f8;
  --lightest-color: #fff;
  --linear-light-to-dark: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --linear-dark-to-light: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --primary-font: "Roboto", sans-serif;
  --transition-dur-and-fun: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  font-family: var(--primary-font);
  font-size: 14px;
  font-style: normal;
}

ul, li {
  list-style: none;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.dark {
  background-color: var(--dark-color);
  color: var(--lightest-color);
}

.light {
  background-color: var(--lightest-color);
  color: var(--middle-grey-color);
}

h1 {
  font-size: 32px;
  font-weight: 700;
  line-height: 38px;
}

h2 {
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
}

@media screen and (min-width: 768px) {
  h1 {
    font-size: 44px;
    line-height: 52px;
  }

  h2 {
    font-size: 24px;
    line-height: 28px;
  }
}

@media screen and (min-width: 1280px) {
  h1 {
    font-size: 64px;
    line-height: 75px;
  }

  h2 {
    font-size: 28px;
    line-height: 33px;
  }
}

a.btn {
  text-align: center;
  text-decoration: none;
}

.btn {
  background: var(--linear-light-to-dark) repeat scroll 0 0 rgba(0, 0, 0, 0);
  box-shadow: 0 4px 5px -6px var(--dark-color);
  border: 0;
  border-radius: 74px;
  align-items: center;
  padding: 1px;
  display: inline-flex;
  overflow: hidden;
}

.btn-in {
  width: 100%;
  height: 100%;
  color: var(--dark-color);
  background-color: inherit;
  transition: color var(--transition-dur-and-fun), background-color var(--transition-dur-and-fun);
  border-radius: 74px;
  padding: 12px 24px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  display: block;
}

.btn-accent:hover .btn-in, .btn-accent:focus .btn-in {
  color: var(--accent-color);
  background-color: var(--lightest-color);
}

.btn-dark .btn-in {
  color: var(--lightest-color);
  background-color: var(--dark-color);
}

.btn-dark:hover .btn-in, .btn-dark:focus .btn-in {
  color: var(--accent-color);
  background-color: var(--lightest-color);
}

.btn-light .btn-in {
  color: var(--dark-color);
  background-color: var(--lightest-color);
}

.btn-light:hover .btn-in, .btn-light:focus .btn-in {
  color: var(--accent-color);
  background-color: var(--dark-color);
}

.btn-round {
  width: 24px;
  height: 24px;
}

.btn-round .btn-in {
  padding: 6px;
  font-size: 10px;
  font-weight: 400;
  line-height: 1;
}

@media screen and (min-width: 768px) {
  .btn-in {
    font-size: 14px;
    line-height: 1.14;
  }

  .btn-round {
    width: 40px;
    height: 40px;
  }

  .btn-round .btn-in {
    padding: 12px;
    font-size: 14px;
  }
}

@media screen and (min-width: 1280px) {
  .btn-in {
    font-size: 16px;
    line-height: 1.5;
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: var(--lightest-color);
  opacity: .2;
  z-index: 10;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.modal {
  z-index: 20;
  background-color: var(--dark-color);
  width: 280px;
  max-height: 100vh;
  border-radius: 16px;
  padding: 52px 16px;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.modal-show {
  display: flex;
}

.modal-close-btn {
  width: 24px;
  height: 24px;
  cursor: pointer;
  color: var(--light-color);
  transition: color var(--transition-dur-and-fun);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 20px;
  right: 16px;
}

.modal-close-btn:hover, .modal-close-btn:focus {
  color: var(--accent-color);
}

.modal-close-btn .modal-close-icon {
  stroke: currentColor;
}

.modal-container {
  box-sizing: border-box;
  width: 248px;
  padding: 0;
  overflow-y: auto;
}

.modal-img {
  object-fit: cover;
  width: 248px;
  height: 315px;
  border-radius: 5px;
  margin-bottom: 22px;
}

.modal-title {
  color: var(--lightest-color);
  margin-bottom: 29px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.15;
}

.modal-details-list {
  margin-bottom: 4px;
}

.modal-details-item {
  align-items: baseline;
  gap: 50px;
  margin-bottom: 12px;
  display: flex;
}

.modal-details-subtitle {
  color: var(--light-grey-color);
  min-width: 90px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.modal-details-item:last-child {
  margin-bottom: 0;
}

.modal-votes {
  color: var(--light-grey-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.modal-votes span {
  background-color: var(--lightest-color);
  color: var(--dark-color);
  border-radius: 5px;
  padding: 1px 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
}

.modal-popularity, .modal-genre {
  color: var(--lightest-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  display: flex;
}

.modal-about {
  text-transform: uppercase;
  color: var(--light-grey-color);
  margin-bottom: 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
}

.modal-about-text {
  color: var(--light-color);
  max-height: 180px;
  margin-bottom: 16px;
  font-size: 14px;
  line-height: 1.4;
  overflow-y: auto;
}

.modal-btn-add {
  color: var(--lightest-color);
}

.modal-btn-remove {
  color: var(--dark-color);
  display: none;
}

@media screen and (min-width: 768px) {
  .modal {
    width: 704px;
    padding: 44px 49px 44px 44px;
  }

  .modal-container {
    display: flex;
  }

  .modal-img {
    width: 294px;
    height: 400px;
    margin-bottom: 0;
  }

  .modal-container {
    width: 100%;
    gap: 16px;
  }

  .modal-title {
    font-size: 24px;
    line-height: 1.16;
  }

  .modal-details-list {
    margin-bottom: 20px;
  }

  .modal-about {
    margin-bottom: 16px;
  }

  .modal-about-text {
    max-width: 301px;
    margin-bottom: 24px;
    font-size: 12px;
    font-weight: 400;
    line-height: 1.67;
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    width: 846px;
    padding: 60px;
  }

  .modal-close-btn {
    top: 40px;
    right: 40px;
  }

  .modal-img {
    width: 375px;
    height: 478px;
  }

  .modal-container {
    gap: 20px;
  }

  .modal-title {
    font-size: 24px;
    line-height: 1.33;
  }

  .modal-about-text {
    max-width: 331px;
    font-size: 14px;
    line-height: 1.43;
  }
}

.rating {
  display: flex;
}

.star, .hero > .star {
  width: 14px;
  height: 14px;
}

.star:not(:last-child) {
  margin-right: 4px;
}

@media screen and (min-width: 768px) {
  .star {
    width: 10px;
    height: 10px;
  }

  .hero > .star {
    width: 14px;
    height: 14px;
  }
}

@media screen and (min-width: 1280px) {
  .star {
    width: 18px;
    height: 18px;
  }

  .hero > .star {
    width: 14px;
    height: 14px;
  }
}

.scroll-up-btn {
  z-index: 99;
  background-color: inherit;
  cursor: pointer;
  border: none;
  padding: 15px;
  transition: transform .5s;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 15px;
  transform: scale(1);
}

.scroll-up-btn:hover {
  transform: scale(1.15);
}

@media screen and (min-width: 320px) {
  .scroll-up-btn {
    width: 24px;
    height: 24px;
  }
}

@media screen and (min-width: 768px) {
  .scroll-up-btn {
    width: 34px;
    height: 34px;
    bottom: 20px;
    right: 15px;
  }

  .icon {
    width: 34px;
    height: 34px;
  }
}

@media screen and (min-width: 1280px) {
  .scroll-up-btn {
    width: 64px;
    height: 64px;
    bottom: 20px;
    right: 30px;
  }

  .icon {
    width: 64px;
    height: 64px;
  }
}

.movies-container {
  flex-flow: column wrap;
  align-content: center;
  row-gap: 16px;
  display: flex;
}

.movie-card {
  width: 280px;
  height: 406px;
  cursor: pointer;
  transition: scale var(--transition-dur-and-fun);
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}

.movie-card:is(:hover, :focus) {
  transform: scale(1.005);
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.movie-card__backdrop {
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  position: absolute;
  bottom: 0;
}

.movie-card__img {
  width: 100%;
}

.movie-card__info {
  width: 100%;
  padding: 0 12px 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.6;
  position: absolute;
  bottom: 0;
}

.movie-card__info__items {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
}

.info-item__name {
  text-transform: uppercase;
  color: var(--lightest-color);
}

.info-item__genreYear {
  color: var(--light-grey-color);
}

.info-item__stars {
  flex-shrink: 0;
  padding-bottom: 4px;
  padding-left: 12px;
  line-height: 1.67;
}

@media screen and (min-width: 768px) {
  .gallery-section {
    padding-top: 36px;
  }

  .movies-container {
    flex-direction: row;
    gap: 28px 16px;
  }

  .movie-card {
    width: 224px;
    height: 325px;
  }

  .movie-card__info {
    line-height: 1.58;
  }

  .info-item__name {
    padding-bottom: 4px;
  }
}

@media screen and (min-width: 1280px) {
  .gallery-section {
    padding-top: 48px;
  }

  .movies-container {
    gap: 32px 15px;
  }

  .movie-card {
    width: 395px;
    height: 574px;
  }

  .movie-card__info {
    padding: 0 20px 20px;
    font-size: 20px;
    line-height: 1.15;
  }
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 100px auto;
  animation: animloader 1s linear infinite;
  display: block;
}

@keyframes animloader {
  0% {
    box-shadow: inset -72px 0 #fff;
  }

  100% {
    box-shadow: inset 48px 0 #fff;
  }
}

.footer {
  text-align: center;
  color: var(--light-grey-color);
  background: inherit;
  padding-top: 40px;
  padding-bottom: 28px;
}

.footer__description {
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__icon {
  fill: var(--accent-color);
  margin-left: 10px;
  margin-right: 10px;
  animation: animHeart 2s infinite forwards;
}

.footer__btn {
  color: var(--light-grey-color);
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
  margin-left: 3px;
  padding: 0;
  position: relative;
}

.footer__btn:after, .footer__btn:before {
  content: "";
  width: 100%;
  height: 2px;
  transition: transform var(--transition-dur-and-fun);
  position: absolute;
  bottom: 0;
  left: 0;
}

.footer__btn:before {
  background-color: var(--accent-color);
  transform-origin: 0;
  transition-delay: 0s;
  transform: scaleX(0);
}

.footer__btn:after {
  background-color: var(--light-grey-color);
  transform-origin: 100%;
  transition-delay: .25s;
  transform: scaleX(1);
}

.footer__btn:hover:before, .footer__btn:focus:before {
  transition-delay: .25s;
  transform: scaleX(1);
}

.footer__btn:hover:after, .footer__btn:focus:after {
  transition-delay: 0s;
  transform: scaleX(0);
}

@media screen and (min-width: 768px) {
  .footer {
    padding-top: 60px;
    padding-bottom: 40px;
    font-size: 16px;
    line-height: 1.2;
  }

  .footer__wrapper {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .footer__description:not(:last-child) {
    margin-right: 3px;
  }
}

@media screen and (min-width: 1280px) {
  .footer {
    padding-top: 100px;
    padding-bottom: 80px;
  }
}

@keyframes animHeart {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.3);
  }

  100% {
    transform: scale(1);
  }
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo__img {
  width: 32px;
  height: 32px;
}

.mob-menu-btn {
  text-transform: uppercase;
  color: var(--light-grey-color);
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
  padding-top: 16px;
  padding-bottom: 16px;
  font-family: Roboto;
}

.theme.btn {
  min-width: 44px;
  max-height: 18px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 4px 3px;
  display: flex;
  position: relative;
}

.theme__icon {
  width: 10px;
  height: 10px;
  stroke: var(--lightest-color);
  fill: none;
}

.theme__switch {
  width: 14px;
  height: 14px;
  opacity: 0;
  position: absolute;
}

.theme__switch.dark {
  opacity: 1;
  transform: rotate(0);
  background-color: rgba(0, 0, 0, 0);
  right: 2px;
}

.theme__switch.light {
  opacity: 1;
  background-color: rgba(0, 0, 0, 0);
  left: 2px;
  transform: rotate(-180deg);
}

.site-nav {
  display: flex;
}

.site-nav__item:not(:last-child) {
  margin-right: 32px;
}

.site-nav__link {
  text-transform: uppercase;
  color: var(--light-grey-color);
  transition: color var(--transition-dur-and-fun), transform var(--transition-dur-and-fun);
  display: flex;
}

.site-nav__link:hover, .site-nav__link:focus {
  color: var(--accent-color);
  transform: scaleX(1.5);
}

.js-nav-link.active-page {
  color: var(--accent-color);
}

.logo__wrapper {
  color: inherit;
  align-items: center;
  display: flex;
}

.logo__name {
  margin-left: 8px;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.2;
  display: none;
  position: relative;
  overflow: hidden;
}

.logo__name:after {
  content: "";
  width: 100%;
  height: 2px;
  transform-origin: 100%;
  background-color: var(--accent-color);
  transition: transform 1s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.mobile-backdrop {
  min-height: 100vh;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  transition: background-color var(--transition-dur-and-fun) .1s, opacity 150ms cubic-bezier(0, 0, .58, 1) .3s;
  background: rgba(255, 255, 255, .2);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.mobile-backdrop.dark {
  background-color: rgba(255, 255, 255, .2);
}

.mobile-backdrop.light {
  background-color: rgba(0, 0, 0, .2);
}

.mobile-backdrop.is-open {
  opacity: 1;
  pointer-events: visible;
  overflow: auto;
}

.mobile-nav {
  width: 65%;
  height: 100vh;
  opacity: 0;
  transition: transform 500ms cubic-bezier(.4, 0, .2, 1) .1s, opacity var(--transition-dur-and-fun) .1s;
  border-radius: 0 8px 8px 0;
  padding-top: 8px;
  padding-left: 20px;
  transform: translateY(-100%);
}

.mobile-nav.dark {
  background-color: var(--dark-color);
}

.mobile-nav.light {
  background-color: var(--light-color);
}

.mobile-backdrop.is-open .mobile-nav {
  opacity: 1;
